<template>
    <div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
            <template slot="top">
                <a-button type="primary"
                          :disabled="true"
                          @click="handleCreate()">新增
                </a-button>
                <div style="height: 10px"></div>
                <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
                <div style="height: 10px"></div>
            </template>
        </table-component>

    </div>
</template>

<script>
    import {getAuthMapList, deleteAuthMap} from '@/api/authMap'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            filterComponent, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '权限',
                        dataIndex: 'auth_format',
                        key: 'auth_format',
                    },
                    {
                        title: '描述1',
                        dataIndex: 'description_1',
                        key: 'description_1',
                    },
                    {
                        title: '描述2',
                        dataIndex: 'description_2',
                        key: 'description_2',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            edit: {visible: true, disable: true,},
                            delete: {visible: true, disable: true,},
                        }
                    }
                ],
                tableRequest: getAuthMapList,
                filterFields: [
                    {label: '关键字', field: 'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCreate() {
                this.modalTitle = '新增字典';
                this.modalVisible = true;
                this.formId = null;
            },
            handleEdit(id) {
                this.modalTitle = '编辑字典';
                this.modalVisible = true;
                this.formId = id;
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'create':
                        this.handleCreate();
                        break;
                    case 'edit':
                        this.handleEdit(row.id);
                        break;
                    case 'delete':
                        deleteAuthMap(row.id)
                            .then(res => {
                                this.$message.success('删除成功');
                                this.$refs.table.reFetch();
                            });
                        break;
                }
            },
        },
    }
</script>

